import React, { useState, useEffect } from 'react';
import * as d3 from 'd3';
import { DropdownButton, Dropdown } from 'react-bootstrap';
import { navigate } from 'gatsby';
import "../styles/index.css";
import { UseDataService } from '../components/UseDataService';
import IconComponent from './IconComponent';


const Filters = ({
    selectedState, setSelectedState,
    selectedLga, setSelectedLga, lgas, // Add lgas here
    selectedService, setSelectedService,
    selectedPopulation, setSelectedPopulation,
    selectedLocation, setSelectedLocation,
    onServiceChange, onPopulationChange, onLocationChange
}) => {

    const states = [
        "All States", "Abia", "Adamawa", "Akwa Ibom", "Anambra", "Bauchi", "Bayelsa", "Benue",
        "Borno", "Cross River", "Delta", "Ebonyi", "Edo", "Ekiti", "Enugu",
        "Federal Capital Territory", "Gombe", "Imo", "Jigawa", "Kaduna", "Kano",
        "Katsina", "Kebbi", "Kogi", "Kwara", "Lagos", "Nasarawa", "Niger", "Ogun",
        "Ondo", "Osun", "Oyo", "Plateau", "Rivers", "Sokoto", "Taraba", "Yobe", "Zamfara"
    ];

    const {
        allServicesCount,
        condomCount,
        serviceCount,
        flheCount,
        matCount,
        needleCount,
        selftestCount,
        htsCount,
        mentalhealthCount,
        prepCount,
        sbccCount,
        stiCount,
        taCount,
        fschoolCount, fnoschoolCount,
        mschoolCount, mnoschoolCount, clhivCount,
        genpopCount, heiCount, idpCount, fswCount,
        msmCount, pesCount, pwidCount, transCount, ovcCount,
        parentsCount, pldCount, plhivCount, sdCount,
        teachersCount, faithCount, fishermenCount, pastoralistsCount,
        transportCount, communityCount, facilityCount, schoolCount
    } = UseDataService();


    const [activeService, setActiveService] = useState(null);
    const [isServicesDropdownOpen, setIsServicesDropdownOpen] = useState(false);
    const [isPopulationsDropdownOpen, setIsPopulationsDropdownOpen] = useState(false);
    const [isLocationsDropdownOpen, setIsLocationsDropdownOpen] = useState(false);



    const onServiceSelect = (service) => {
        setSelectedService(service);
        setActiveService(service); // Set the active service to highlight the clicked icon
        setSelectedPopulation('All Populations');
        setSelectedLocation('All Locations');
        setIsServicesDropdownOpen(false);
        onServiceChange(service);
    };

    const onPopulationSelect = (population) => {
        setSelectedPopulation(population);
        setActiveService(population); // Set the active service to highlight the clicked icon
        setSelectedService('All Services');
        setSelectedLocation('All Locations');
        setIsPopulationsDropdownOpen(false);
        onPopulationChange(population);
    };

    const onLocationSelect = (location) => {
        setSelectedLocation(location);
        setActiveService(location); // Set the active service to highlight the clicked icon
        setSelectedService('All Services');
        setSelectedPopulation('All Populations');
        setIsLocationsDropdownOpen(false);
        onLocationChange(location);
    };

    const onLgaSelect = (lga) => {
        setSelectedLga(lga);
        // Navigate to the LGA-specific page
        navigate(`/${selectedState.toLowerCase()}/${lga.toLowerCase().replace(/\s+/g, '-')}`);
    };    

    const toggleServicesDropdown = () => {
        setIsServicesDropdownOpen(!isServicesDropdownOpen);
    };

    const togglePopulationsDropdown = () => {
        setIsPopulationsDropdownOpen(!isPopulationsDropdownOpen);
    };

    const toggleLocationsDropdown = () => {
        setIsLocationsDropdownOpen(!isLocationsDropdownOpen);
    };



    return (
        <div>
            <h3>State</h3>
            <DropdownButton id="dropdown-basic-button_filter_state" title={selectedState || "Select State"} className="scrollable-dropdown">
                {states.map(state => (
                    <Dropdown.Item
                        key={state}
                        onClick={() => {
                            setSelectedState(state);
                            navigate(`/${state.toLowerCase().replace(/\s+/g, '-')}`); // LGA page navigation
                        }}>
                        {state}
                    </Dropdown.Item>
                ))}
            </DropdownButton>

            <h3>Local Government Area</h3>
            <DropdownButton id="dropdown-basic-button_filter_lga" title={selectedLga || "Select LGA"} className="scrollable-dropdown">
    {lgas && lgas.map(lga => (
        <Dropdown.Item key={lga} onClick={() => onLgaSelect(lga)}>
            {lga}
        </Dropdown.Item>
    ))}
</DropdownButton>



            <h3>Service</h3>
            <button onClick={toggleServicesDropdown} className="dropdown-toggle">
                {selectedService || "Services"}
            </button>
            {isServicesDropdownOpen && (
                <div className="bubbles">

                    <div className={`bubble-container ${selectedService === "All Services" ? "selected" : ""}`} onClick={() => onServiceSelect("All Services")}>
                        <IconComponent service="All Services" className="bubble" alt="All Services" />
                        <div className="label">All Services</div>
                        <div className="info">All services - {allServicesCount} service providers (Nigeria)</div>
                    </div>
                    <div className={`bubble-container ${selectedService === "Condom Distribution" ? "selected" : ""}`} onClick={() => onServiceSelect("Condom Distribution")}>
                        <IconComponent service="Condom Distribution" className="bubble" alt="Condom Distribution" />
                        <div className="label">Condom Distribution</div>
                        <div className="info">Condom distribution - {condomCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Family Life and HIV/AIDS Education (FLHE)/Sexuality Education" ? "selected" : ""}`} onClick={() => onServiceSelect("Family Life and HIV/AIDS Education (FLHE)/Sexuality Education")}>
                        <IconComponent service="FLHE / Sexuality Education" className="bubble" alt="FLHE" />
                        <div className="label">FLHE / Sexuality Education</div>
                        <div className="info">FLHE / Sexuality Education - {flheCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Gender and Human Rights (GHR)" ? "selected" : ""}`} onClick={() => onServiceSelect("Gender and Human Rights (GHR)")}>
                        <IconComponent service="Gender and Human Rights" className="bubble" alt="Gender" />
                        <div className="label">Gender and Human Rights</div>
                        <div className="info">Gender and Human Rights - {serviceCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Harm Reduction: Medication-Assisted Treatment (MAT)" ? "selected" : ""}`} onClick={() => onServiceSelect("Harm Reduction: Medication-Assisted Treatment (MAT)")}>
                        <IconComponent service="Harm Reduction: Medication-Assisted Treatment (MAT)" className="bubble" alt="MAT" />
                        <div className="label">MAT</div>
                        <div className="info">Harm Reduction: MAT - {matCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Harm Reduction: Needle and Syringe Exchange" ? "selected" : ""}`} onClick={() => onServiceSelect("Harm Reduction: Needle and Syringe Exchange")}>
                        <IconComponent service="Harm Reduction: Needle and Syringe Exchange" className="bubble" alt="Harm Reduction: Needle and Syringe Exchange" />
                        <div className="label">Needle and Syringe Exchange</div>
                        <div className="info">Harm Reduction: Needle and Syringe Exchange - {needleCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "HIV Self-Testing (HIVST)" ? "selected" : ""}`} onClick={() => onServiceSelect("HIV Self-Testing (HIVST)")}>
                        <IconComponent service="HIV Self-Testing (HIVST)" className="bubble" alt="HIV Self-Testing (HIVST)" />
                        <div className="label">HIVST</div>
                        <div className="info"> HIV Self-Testing (HIVST) - {selftestCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "HIV Testing Services (HTS)" ? "selected" : ""}`} onClick={() => onServiceSelect("HIV Testing Services (HTS)")}>
                        <IconComponent service="HIV Testing Services (HTS)" className="bubble" alt="HIV Testing Services (HTS)" />
                        <div className="label">HIV Testing Services (HTS)</div>
                        <div className="info"> HIV Testing Services (HTS) - {htsCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Mental Health Services" ? "selected" : ""}`} onClick={() => onServiceSelect("Mental Health Services")}>
                        <IconComponent service="Mental Health Services" className="bubble" alt="Mental Health Services" />
                        <div className="label">Mental Health Services</div>
                        <div className="info"> Mental Health Services - {mentalhealthCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Pre-Exposure Prophylaxis (PrEP)" ? "selected" : ""}`} onClick={() => onServiceSelect("Pre-Exposure Prophylaxis (PrEP)")}>
                        <IconComponent service="Pre-Exposure Prophylaxis (PrEP)" className="bubble" alt="Pre-Exposure Prophylaxis (PrEP)" />
                        <div className="label">PrEP</div>
                        <div className="info"> Pre-Exposure Prophylaxis (PrEP) - {prepCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Social and Behaviour Change Communication (SBCC)" ? "selected" : ""}`} onClick={() => onServiceSelect("Social and Behaviour Change Communication (SBCC)")}>
                        <IconComponent service="Social and Behaviour Change Communication (SBCC)" className="bubble" alt="Social and Behaviour Change Communication (SBCC)" />
                        <div className="label">SBCC</div>
                        <div className="info"> Social and Behaviour Change Communication (SBCC) - {sbccCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "STI Screening and Treatment" ? "selected" : ""}`} onClick={() => onServiceSelect("STI Screening and Treatment")}>
                        <IconComponent service="STI Screening and Treatment" className="bubble" alt="STI Screening and Treatment" />
                        <div className="label">STI Screening and Treatment</div>
                        <div className="info"> STI Screening and Treatment - {stiCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Technical Assistance (TA)" ? "selected" : ""}`} onClick={() => onServiceSelect("Technical Assistance (TA)")}>
                        <IconComponent service="Technical Assistance (TA)" className="bubble" alt="Technical Assistance (TA)" />
                        <div className="label">Technical Assistance</div>
                        <div className="info"> Technical Assistance (TA) - {taCount} service providers (Nigeria)</div>
                    </div>
                </div>
            )}
            <h3>Target Population</h3>
            <button onClick={togglePopulationsDropdown} className="dropdown-toggle">
                {selectedPopulation || "Target Population"}
            </button>
            {isPopulationsDropdownOpen && (
                <div className="bubbles">
                    <div className={`bubble-container ${selectedPopulation === "Adolescent Girls and Young Women (AGYW) in school" ? "selected" : ""}`} onClick={() => onPopulationSelect("Adolescent Girls and Young Women (AGYW) in school")}>
                        <IconComponent service="Adolescent Girls and Young Women (AGYW) in school" className="bubble" alt="Adolescent Girls and Young Women (AGYW) in school" />
                        <div className="label">Adolescent Girls and Young Women in school</div>
                        <div className="info"> AGYW in school - {fschoolCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Adolescent Girls and Young Women (AGYW) out of school" ? "selected" : ""}`} onClick={() => onPopulationSelect("Adolescent Girls and Young Women (AGYW) out of school")}>
                        <IconComponent service="Adolescent Girls and Young Women (AGYW) out of school" className="bubble" alt="Adolescent Girls and Young Women (AGYW) out of school" />
                        <div className="label">Adolescent Girls and Young Women out of school</div>
                        <div className="info"> AGYW out of school - {fnoschoolCount} service providers (Nigeria)</div>
                    </div>


                    <div className={`bubble-container ${selectedPopulation === "Adolescent Boys and Young Men (ABYM) in school" ? "selected" : ""}`} onClick={() => onPopulationSelect("Adolescent Boys and Young Men (ABYM) in school")}>
                        <IconComponent service="Adolescent Boys and Young Men (ABYM) in school" className="bubble" alt="Adolescent Boys and Young Men (ABYM) in school" />
                        <div className="label">Adolescent Boys and Young Men in school</div>
                        <div className="info"> ABYM in school - {mschoolCount} service providers (Nigeria)</div>
                    </div>


                    <div className={`bubble-container ${selectedPopulation === "Adolescent Boys and Young Men (ABYM) out of school" ? "selected" : ""}`} onClick={() => onPopulationSelect("Adolescent Boys and Young Men (ABYM) out of school")}>
                        <IconComponent service="Adolescent Boys and Young Men (ABYM) out of school" className="bubble" alt="Adolescent Boys and Young Men (ABYM) out of school" />
                        <div className="label">Adolescent Boys and Young Men out of school</div>
                        <div className="info"> ABYM out of school - {mnoschoolCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Children living with HIV" ? "selected" : ""}`} onClick={() => onPopulationSelect("Children living with HIV")}>
                        <IconComponent service="Children living with HIV" className="bubble" alt="Children living with HIV" />
                        <div className="label">Children living with HIV</div>
                        <div className="info"> Children living with HIV - {clhivCount} service providers (Nigeria)</div>
                    </div>


                    <div className={`bubble-container ${selectedPopulation === "General Population" ? "selected" : ""}`} onClick={() => onPopulationSelect("General Population")}>
                        <IconComponent service="General Population" className="bubble" alt="General Population" />
                        <div className="label">General Population</div>
                        <div className="info"> General Population - {genpopCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "HIV-exposed infants" ? "selected" : ""}`} onClick={() => onPopulationSelect("HIV-exposed infants")}>
                        <IconComponent service="HIV-exposed infants" className="bubble" alt="HIV-exposed infants" />
                        <div className="label">HIV-exposed infants</div>
                        <div className="info">HIV-exposed infants - {heiCount} service providers (Nigeria)</div>
                    </div>


                    <div className={`bubble-container ${selectedPopulation === "Internally Displaced People (IDP)" ? "selected" : ""}`} onClick={() => onPopulationSelect("Internally Displaced People (IDP)")}>
                        <IconComponent service="Internally Displaced People (IDP)" className="bubble" alt="Internally Displaced People (IDP)" />
                        <div className="label">Internally Displaced People</div>
                        <div className="info">IDP - {idpCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Key Populations - Female Sex Workers (FSW)" ? "selected" : ""}`} onClick={() => onPopulationSelect("Key Populations - Female Sex Workers (FSW)")}>
                        <IconComponent service="Key Populations - Female Sex Workers (FSW)" className="bubble" alt="Key Populations - Female Sex Workers (FSW)" />
                        <div className="label">FSW</div>
                        <div className="info">Female Sex Workers - {fswCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Key Populations - Men who have Sex with Men (MSM)" ? "selected" : ""}`} onClick={() => onPopulationSelect("Key Populations - Men who have Sex with Men (MSM)")}>
                        <IconComponent service="Key Populations - Men who have Sex with Men (MSM)" className="bubble" alt="Key Populations - Men who have Sex with Men (MSM)" />
                        <div className="label">MSM</div>
                        <div className="info">Men who have Sex with Men - {msmCount} service providers (Nigeria)</div>
                    </div>
                    <div className={`bubble-container ${selectedService === "Key Populations - People in enclosed settings" ? "selected" : ""}`} onClick={() => onPopulationSelect("Key Populations - People in enclosed settings")}>
                        <IconComponent service="Key Populations - People in enclosed settings" className="bubble" alt="People in enclosed settings" />
                        <div className="label">People in enclosed settings</div>
                        <div className="info">People in enclosed settings - {pesCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedService === "Key Populations - People Who Inject Drugs (PWID)" ? "selected" : ""}`} onClick={() => onPopulationSelect("Key Populations - People Who Inject Drugs (PWID)")}>
                        <IconComponent service="Key Populations - People Who Inject Drugs (PWID)" className="bubble" alt="People Who Inject Drugs (PWID)" />
                        <div className="label">People Who Inject Drugs</div>
                        <div className="info">People Who Inject Drugs - {pwidCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Key Populations - Transgender" ? "selected" : ""}`} onClick={() => onPopulationSelect("Key Populations - Transgender")}>
                        <IconComponent service="Key Populations - Transgender" className="bubble" alt="Transgender" />
                        <div className="label">Transgender</div>
                        <div className="info">Transgender - {transCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Orphans and Vulnerable Children (OVC)" ? "selected" : ""}`} onClick={() => onPopulationSelect("Orphans and Vulnerable Children (OVC)")}>
                        <IconComponent service="Orphans and Vulnerable Children (OVC)" className="bubble" alt="Orphans and Vulnerable Children (OVC)" />
                        <div className="label">OVC</div>
                        <div className="info">Orphans and Vulnerable Children - {ovcCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Parents" ? "selected" : ""}`} onClick={() => onPopulationSelect("Parents")}>
                        <IconComponent service="Parents" className="bubble" alt="Parents" />
                        <div className="label">Parents</div>
                        <div className="info">Parents - {parentsCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "People living with disabilities" ? "selected" : ""}`} onClick={() => onPopulationSelect("People living with disabilities")}>
                        <IconComponent service="People living with disabilities" className="bubble" alt="People living with disabilities" />
                        <div className="label">People living with disabilities</div>
                        <div className="info">People living with disabilities - {pldCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "People Living with HIV (PLHIV)" ? "selected" : ""}`} onClick={() => onPopulationSelect("People Living with HIV (PLHIV)")}>
                        <IconComponent service="People Living with HIV (PLHIV)" className="bubble" alt="People Living with HIV (PLHIV)" />
                        <div className="label">People Living with HIV (PLHIV)</div>
                        <div className="info">People Living with HIV - {plhivCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Serodiscordant couples" ? "selected" : ""}`} onClick={() => onPopulationSelect("Serodiscordant couples")}>
                        <IconComponent service="Serodiscordant couples" className="bubble" alt="Serodiscordant couples" />
                        <div className="label">Serodiscordant couples</div>
                        <div className="info">Serodiscordant couples - {sdCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Teachers" ? "selected" : ""}`} onClick={() => onServiceSelect("Teachers")}>
                        <IconComponent service="Teachers" className="bubble" alt="Teachers" />
                        <div className="label">Teachers</div>
                        <div className="info">Teachers - {teachersCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Faith Leaders" ? "selected" : ""}`} onClick={() => onPopulationSelect("Faith Leaders")}>
                        <IconComponent service="Faith Leaders" className="bubble" alt="Faith Leaders" />
                        <div className="label">Faith Leaders</div>
                        <div className="info">Faith Leaders - {faithCount} service providers (Nigeria)</div>
                    </div>

                    <div className={`bubble-container ${selectedPopulation === "Fishermen" ? "selected" : ""}`} onClick={() => onPopulationSelect("Fishermen")}>
                        <IconComponent service="Fishermen" className="bubble" alt="Fishermen" />
                        <div className="label">Fishermen</div>
                        <div className="info">Fishermen - {fishermenCount} service providers (Nigeria)</div>
                    </div>


                    <div className={`bubble-container ${selectedPopulation === "Pastoralists" ? "selected" : ""}`} onClick={() => onPopulationSelect("Pastoralists")}>
                        <IconComponent service="Pastoralists" className="bubble" alt="Pastoralists" />
                        <div className="label">Pastoralists</div>
                        <div className="info">Pastoralists - {pastoralistsCount} service providers (Nigeria)</div>
                    </div>
                    <div className={`bubble-container ${selectedPopulation === "Transport Workers" ? "selected" : ""}`} onClick={() => onPopulationSelect("Transport Workers")}>
                        <IconComponent service="Transport Workers" className="bubble" alt="Transport Workers" />
                        <div className="label">Transport Workers</div>
                        <div className="info">Transport Workers - {transportCount} service providers (Nigeria)</div>
                    </div>

                </div>
            )}

            <h3>Location</h3>
            <button onClick={toggleLocationsDropdown} className="dropdown-toggle">
                {selectedLocation || "Location"}
            </button>
            {isLocationsDropdownOpen && (
                <div className="bubbles">
                    <div className={`bubble-container ${selectedLocation === "Community" ? "selected" : ""}`} onClick={() => onLocationSelect("Community")}>
                        <IconComponent service="Community" className="bubble" alt="Community" />
                        <div className="label">Community</div>
                        <div className="info">Community - {communityCount} service providers (Nigeria)</div>
                    </div>
                    <div className={`bubble-container ${selectedLocation === "Facility" ? "selected" : ""}`} onClick={() => onLocationSelect("Facility")}>
                        <IconComponent service="Facility" className="bubble" alt="Facility" />
                        <div className="label">Facility</div>
                        <div className="info">Facility - {facilityCount} service providers (Nigeria)</div>
                    </div>
                    <div className={`bubble-container ${selectedLocation === "School" ? "selected" : ""}`} onClick={() => onLocationSelect("School")}>
                        <IconComponent service="School" className="bubble" alt="School" />
                        <div className="label">School</div>
                        <div className="info">School - {schoolCount} service providers (Nigeria)</div>
                    </div>
                </div>
            )}

            <div className="submissionsLink">
                <a
                    href="/mhps2023/submissions" >
                    View submissions status
                </a>
            </div>

        </div>

    );
};

export default Filters;